import React, { Component } from "react";
import AceEditor from "react-ace";
import "./CodeEditor.scss";
import * as Actions from "../../store/CodeEditor/CodeEdiorAction";
import * as UserActions from "../../store/User/UserAction";
import { bindActionCreators } from "redux";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import "ace-builds/src-noconflict/mode-jsx";
import "ace-builds/src-min-noconflict/ext-searchbox";
import "ace-builds/src-min-noconflict/ext-language_tools";
import "ace-builds/src-noconflict/theme-tomorrow";
import Button from "@material-ui/core/Button";
import SelectInput from "../Atoms/SelectInput";
import codeIcon from "../../assets/images/codeIcon.svg";
import AudioAnalyser from "../Preview/AudioAnalyser";
import audio from "../../assets/images/audio.png";
import WebcamComponent from "./WebcamComponent";
import { TimerComponent } from "./timerComponent";
import CircularProgress from "@material-ui/core/CircularProgress";
import ScreenRecording from "../Test/screenRecording";

let codeSkipableValue = parseInt(process.env.REACT_APP_CODE_SKIP_TIME)
  ? parseInt(process.env.REACT_APP_CODE_SKIP_TIME)
  : 60;
const suportLang = [
  { label: "javascript", value: "javascript" },
  { label: "java", value: "java" },
  { label: "python", value: "python" },
  { label: "xml", value: "xml" },
  { label: "ruby", value: "ruby" },
  { label: "sass", value: "sass" },
  { label: "markdown", value: "markdown" },
  { label: "mysql", value: "mysql" },
  { label: "json", value: "json" },
  { label: "html", value: "html" },
  { label: "handlebars", value: "handlebars" },
  { label: "go", value: "golang" },
  { label: "c#", value: "csharp" },
  { label: "elixir", value: "elixir" },
  { label: "typescript", value: "typescript" },
  { label: "css", value: "css" },
  { label: "c", value: "c_cpp" },
  { label: "c++", value: "c_cpp" },
  { label: "perl", value: "perl" },
  { label: "sql", value: "mysql" },
];
suportLang.forEach((lang) => {
  require(`ace-builds/src-noconflict/mode-${lang.value}`);
  require(`ace-builds/src-noconflict/snippets/${lang.value}`);
});
let remainingGlobal = 0,
  durationGlobal = 0;
let Interval = "",
  scheduleIdGlobal = "",
  candidateIdGlobal = "",
  timedate = "",
  activeStep = 0,
  ScreenFileGlobal = "",
  ScreenFileNameGlobal = "";
class CodeEditor extends Component {
  constructor(props) {
    super(props);
    this.clockRef = React.createRef();
    this.mediaRecorderRef = React.createRef();
    this.timer = 0;
    this.state = {
      code: "",
      input: "",
      data: null,
      language: null,
      responseId: "",
      ScheduleId: "",
      CandidateId: "",
      languageOptions: [],
      tab: true,
      output: "",
      restartTimer: false,
      isPlaying: true,
      audioNew: null,
      run: false,
      questionsList: [],
      current: 0,
      currentData: null,
      totalLength: 0,
      autocomplete: false,
      skip: 0,
      executeId: null,
      countExecute: 10,
      totalExecuted: 0,
      disablebtn: true,
      capturing: false,
      recordedChunks: [],
      videofilename: "",
      remainingTime: 0,
      submitClicked: false,
      timedate: new Date().toISOString(),
      events: [],
    };
    this.vid = React.createRef();
    this.audio = React.createRef();
    this.webcamRef = React.createRef();
    this.childRef = React.createRef();
  }
  componentDidMount() {
    timedate = this.state.timedate;
    const { id, questionType, candidateId, scheduleId } =
      this.props &&
      this.props.data &&
      this.props.data.data &&
      this.props.data.data.response;
    const ques =
      questionType &&
      questionType.length > 0 &&
      questionType.find((el) => el.name && el.name.toLowerCase() === "coding");

    const data =
      this.props && this.props.data && this.props.data.codingQuestions;
    const meta = data && data.length > 0 && data[0] && data[0].metadata;
    const languageSupport = [];
    meta &&
      meta.allowedLanguages &&
      meta.allowedLanguages.length > 0 &&
      meta.allowedLanguages.map((el) => {
        const lang = el.name.toLowerCase().includes("python")
          ? { label: "python", value: "python" }
          : el.name.toLowerCase().includes("visual basic")
          ? null
          : suportLang &&
            suportLang.find((item) => item.label === el.name.toLowerCase());

        if (lang) {
          languageSupport.push({
            label: el && el.compiler,
            lang: lang && lang.value,
            value: el && el.code,
          });
        }
      });
    this.setState({
      data: this.props && this.props.data,
      responseId: id,
      ScheduleId: scheduleId,
      CandidateId: candidateId,
      questionsList: data,
      current: 1,
      currentData: data && data[0],
      totalLength: data.length,
      languageOptions: languageSupport,
      autocomplete: meta && meta.autocomplete,
      countExecute:
        ques && ques.allowedExecutions ? parseInt(ques.allowedExecutions) : 10,
      skip: ques.skipable,
    });

    activeStep = 1;
    scheduleIdGlobal = scheduleId;
    candidateIdGlobal = candidateId;
    const timeOut = ques.skipable ? codeSkipableValue * 1000 : 0;
    timeOut &&
      setTimeout(() => {
        this.setState({ disablebtn: false });
      }, timeOut);
    this.getMicrophone();
    document.addEventListener("contextmenu", (e) => {
      e.preventDefault();
      const { events } = this.state;
      events.push({
        type: "browser",
        level: 2,
        metadata: "Right click triggered",
        timestamp: new Date().toISOString(),
        time: this.state.timerInput - this.state.remainingTime,
      });
      id && this.setState({ events: events });
    });
    document.addEventListener("visibilitychange", (event) => {
      if (document.visibilityState == "visible") {
        const { events } = this.state;
        events.push({
          type: "browser",
          level: 2,
          metadata: "tab is active",
          timestamp: new Date().toISOString(),
          time: this.state.timerInput - this.state.remainingTime,
        });
        id && this.setState({ events: events });
      } else {
        const { events } = this.state;
        events.push({
          type: "browser",
          level: 2,
          metadata: "tab is inactive",
          timestamp: new Date().toISOString(),
          time: this.state.timerInput - this.state.remainingTime,
        });
        id && this.setState({ events: events });
      }
    });
  }
  selectionOperation = (type) => {
    const { responseId } = this.state;
    const { events } = this.state;
    events.push({
      type: "browser",
      level: 2,
      metadata: `${type} action triggered`,
      timestamp: new Date().toISOString(),
      time: this.state.timerInput - this.state.remainingTime,
    });
    responseId && this.setState({ events: events });
  };
  componentDidUpdate(prevProps, prevState) {
    const cur = this.props && this.props.codeReducer;
    const prev = prevProps && prevProps.codeReducer;
    if (
      (this.state.run &&
        prevState.code !== this.state.code &&
        this.state.code) ||
      prevState.input !== this.state.input
    ) {
      this.setState({ run: false, output: "" });
    }
    if (
      prev.submitCode !== cur.submitCode &&
      cur.submitCode &&
      cur.isSubmitCode &&
      cur.submitCode.data
    ) {
      const { data } = cur.submitCode;
      this.setState({
        executeId: data && data._id,
        tab: false,
      });
      this.props.CodeAction.getEditor({
        id: data && data._id,
      });
    }
    if (
      prev.getCode !== cur.getCode &&
      cur.getCode &&
      cur.isGetCode &&
      cur.getCode.data
    ) {
      const data = cur.getCode.data;
      const { output, status } = data;
      if (status === "EXECUTED") {
        this.setState({
          output: output,
          totalExecuted: parseInt(data.executionCount),
        });
      } else {
        const _this = this;
        setTimeout(() => {
          _this.props.CodeAction.getEditor({
            id: _this.state.executeId,
          });
        }, 1000);
      }
    }
  }

  handleRun = () => {
    const { responseId, currentData, input, code, language } = this.state;
    this.setState({ run: true });
    remainingGlobal = durationGlobal - this.state.remainingTime;
    this.props.CodeAction.submitEditor({
      responseId: responseId,
      questionId: currentData && currentData.id,
      stdin: input,
      languageCode: language && language.value,
      codeText: code,
      timeTaken: remainingGlobal.toString(),
    });
  };
  inputHandler = (e) => {
    this.setState({ input: e.target.value });
  };
  async getMicrophone() {
    const audio = await navigator.mediaDevices.getUserMedia({
      audio: true,
    });
    this.setState({ audioNew: audio });
  }
  onLoad = () => {
    //onload
  };
  onChange = (newValue) => {
    this.setState({ code: newValue });
  };
  // handleTimer = () => {};
  handleTimer = async () => {
    await this.setState({
      restartTimer: false,
      isPlaying: false,
      disablebtn: true,
      run: true,
    });
    const { responseId, currentData, input, code, language } = this.state;
    remainingGlobal = durationGlobal - this.state.remainingTime;
    if (code && responseId && currentData && language) {
      this.props.CodeAction.submitEditor({
        responseId: responseId,
        questionId: currentData && currentData.id,
        stdin: input,
        languageCode: language && language.value,
        codeText: code,
        timeTaken: remainingGlobal.toString(),
      });
    }
    setTimeout(() => {
      this.handleStopCaptureClick();
    }, 2000);
    this.childRef.current.stopCapture();
  };

  handleStopCaptureClick = () => {
    if (
      this.mediaRecorderRef &&
      this.mediaRecorderRef.current &&
      this.mediaRecorderRef.current.state == "recording"
    ) {
      this.mediaRecorderRef.current.stop();
      this.setState({
        capturing: false,
      });
    }
  };
  handleDataAvailable = async ({ data }) => {
    remainingGlobal = durationGlobal - this.state.remainingTime;
    if (data.size > 0) {
      await this.setState({
        recordedChunks: this.state.recordedChunks.concat(data),
      });
      // setTimeout(()=>{
      if (this.state.current === this.state.totalLength) {
        this.handleFinish(this.state.questionsList);
      } else {
        this.handleNext(this.state.questionsList);
      }
      // },2000)
    }
  };
  handleNext = async (questiondata) => {
    await this.setState({
      restartTimer: true,
      isPlaying: true,
    });

    await this.handleDownload(questiondata);
    let quesdata = [];

    questiondata.map((item) => {
      quesdata.push(item.id);
    });
    this.handleStartCaptureClick();
    this.getStepContent(questiondata, this.state.current);
    const { totalLength, current, questionsList } = this.state;

    if (current < totalLength && questionsList && questionsList[current]) {
      const meta = questionsList[current] && questionsList[current].metadata;
      const languageSupport = [];
      meta &&
        meta.allowedLanguages &&
        meta.allowedLanguages.length > 0 &&
        meta.allowedLanguages.map((el) => {
          // const lang =
          //   suportLang &&
          //   suportLang.find((item) => item.label === el.name.toLowerCase());

          // return { label: el.compiler, lang: lang.value, value: el.code };
          const lang = el.name.toLowerCase().includes("python")
            ? { label: "python", value: "python" }
            : el.name.toLowerCase().includes("visual basic")
            ? null
            : suportLang &&
              suportLang.find((item) => item.label === el.name.toLowerCase());

          if (lang) {
            languageSupport.push({
              label: el && el.compiler,
              lang: lang && lang.value,
              value: el && el.code,
            });
          }
        });
      this.setState({
        current: current + 1,
        currentData: questionsList[current],
        languageOptions: languageSupport,
        autocomplete: meta && meta.autocomplete,
        code: "",
        tab: true,
        input: "",
        output: "",
        totalExecuted: 0,
        disablebtn: true,
        language: null,
      });
      activeStep = current + 1;
    }
  };
  handleFinish = async (questiondata) => {
    this.setState({
      submitClicked: true,
    });
    await this.handleDownload(questiondata);
    let quesdata = [];

    questiondata.map((item) => {
      quesdata.push(item.id);
    });
    const question = [];
    question.push(this.getStepContent(this.state.current));

    this.props.submitClickedHandler();
  };
  handleDownload = (questiondata) => {
    let quesdata = [];

    questiondata.map((item) => {
      quesdata.push(item.id);
    });
    if (this.state.recordedChunks.length) {
      const blob = new Blob(this.state.recordedChunks, {
        type: "video/webm",
      });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      document.body.appendChild(a);
      a.style = "display: none";
      a.href = url;
      window.URL.revokeObjectURL(url);
      this.setState({ recordedChunks: [] });
      var file = new File(
        [blob],
        `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.current}.mp4`
      );
      var filedata = `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.current}.mp4`;
      this.setState({ videofilename: filedata });
      this.handlePriorUpload(
        file,
        `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.current}.mp4`,
        quesdata
      );
    }
  };
  getStepContent(questiondata, stepIndex) {
    if (questiondata.length > 0) {
      let steparray = [];
      questiondata.map((item) => {
        steparray.push(item.QuestionText);
      });
      const value = steparray[this.state.current];
      switch (this.state.activeStep) {
        case this.state.activeStep:
          return (
            <div>
              <div className='question-text'>
                Q{this.state.activeStep + 1}. {value}
              </div>
            </div>
          );
        default:
          return "Unknown stepIndex";
      }
    }
  }
  handleStartCaptureClick = () => {
    let isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    this.setState({ events: [] });
    const payload = {
      questionId: this.state.currentData.id,
      responseId: this.state.responseId,
      type: "coding",
      questionStatus: 2,
    };
    this.props.UserAction.updateResponse(payload);
    if (this.mediaRecorderRef) {
      this.mediaRecorderRef.current = new MediaRecorder(
        this.webcamRef.current.stream,
        {
          mimeType: isSafari
            ? 'video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
            : "video/webm",
          audioBitsPerSecond: 128000,
          videoBitsPerSecond: 625000,
        }
      );
    }

    if (
      this.mediaRecorderRef &&
      this.mediaRecorderRef.current &&
      this.mediaRecorderRef.current.state == "inactive"
    ) {
      this.mediaRecorderRef.current.start();
      this.mediaRecorderRef.current.addEventListener(
        "dataavailable",
        this.handleDataAvailable
      );
    }

    this.setState(
      {
        capturing: true,
      },
      () => {
        this.childRef.current.startCapture();
      }
    );

    ScreenFileGlobal = "";
    ScreenFileNameGlobal = "";

    // }
  };
  handlePriorUpload = async (file, name, quesdata) => {
    if (!this.state.disablebtn) {
      this.setState({
        disablebtn: true,
      });
      let time = codeSkipableValue * 1000;

      setTimeout(() => {
        this.setState({
          disablebtn: false,
        });
      }, time);
    } else {
      let time = codeSkipableValue * 1000;

      setTimeout(() => {
        this.setState({
          disablebtn: false,
        });
      }, time);
    }
    var formdata = new FormData();
    formdata.append("video", file, name);
    if (ScreenFileGlobal) {
      formdata.append("video", ScreenFileGlobal, ScreenFileNameGlobal);
    }
    formdata.append("timeTaken", remainingGlobal);
    formdata.append("events", JSON.stringify(this.state.events));
    const payload = {
      questionId: this.state.currentData.id,
      responseId: this.state.responseId,
      type: "coding",
      questionStatus: 3,
      timeTaken: remainingGlobal,
      // videoFile: file,
      // videoFileName: name,
    };
    let quesNo = this.state.current;
    let questionType = "coding";
    let indexDbData = {
      questionId: this.state.currentData.id,
      responseId: this.state.responseId,
      type: "coding",
      questionStatus: 3,
      timeTaken: remainingGlobal,
      videoFile: file,
      videoFileName: name,
      screenFile: ScreenFileGlobal,
      screenFileName: ScreenFileNameGlobal,
      events: JSON.stringify(this.state.events),
    };
    this.props.UserAction.addEvent({
      responseId: this.state.responseId,
      event: {
        type: "debug",
        level: 3,
        metadata: {
          type: "Question Submit Start",
          questionId: this.state.currentData.id,
          responseId: this.state.responseId,
          type: "coding",
          questionStatus: 3,
          timeTaken: remainingGlobal,
          events: this.state.events,
        },
        timestamp: new Date().toISOString(),
      },
    });

    this.props.upload(formdata, payload, indexDbData, quesNo, questionType);

    // this.setState({
    //   faceNotDetected: 0,
    // });
  };
  onScreenAccess() {
    console.log("got Screen access");
  }
  onRecordStart() {
    console.log("started recording");
  }
  onRecordStop() {
    console.log("stopped recording");
  }
  onUserStop() {
    console.log("user stopped screen access");
    ScreenFileGlobal = "";
    ScreenFileNameGlobal = "";
  }
  async onVideoFile(blob) {
    let ScreenFileTemp = new File(
      [blob],
      `$${scheduleIdGlobal}-${candidateIdGlobal}-${timedate}-${activeStep}.mp4`
    );
    let ScreenFileNameTemp = `${scheduleIdGlobal}-${candidateIdGlobal}-${timedate}-${activeStep}Screen.mp4`;

    ScreenFileGlobal = ScreenFileTemp;
    ScreenFileNameGlobal = ScreenFileNameTemp;
  }
  onAbort() {
    console.log("recieved abort callback");
  }
  handleTimeUpdate = (time, remainingTime) => {
    if (this.state.time != time) {
      this.setState({ time: time, remainingTime: remainingTime });
    }
  };

  render() {
    const {
      code,
      language,
      tab,
      input,
      output,
      languageOptions,
      restartTimer,
      isPlaying,
      audioNew,
      run,
      autocomplete,
      currentData,
      current,
      totalLength,
      countExecute,
      totalExecuted,
      disablebtn,
    } = this.state;
    const duration =
      currentData && currentData.suggestedDuration
        ? currentData.suggestedDuration
        : 0;
    durationGlobal = duration;
    return (
      <div
        className='CodingMainPage'
        onCopy={() => this.selectionOperation("Copy")}
        onCut={() => this.selectionOperation("Cut")}
        onPaste={() => this.selectionOperation("Paste")}
      >
        <ScreenRecording
          ref={this.childRef}
          onScreenAccess={this.onScreenAccess}
          onUserStop={this.onUserStop}
          onRecordStart={this.onRecordStart}
          onRecordStop={this.onRecordStop}
          onVideoFile={this.onVideoFile}
          onAbort={this.onAbort}
        />
        <div className='CodingArea'>
          <div className='QuestionArea'>
            <div className='TitleQuestion'>
              <div className='textTitle'>
                Coding Question {current}/{totalLength}
              </div>
            </div>
            <div className='QuestionContent'>
              <div
                className='QuestionMain'
                dangerouslySetInnerHTML={{
                  __html: currentData && currentData.text,
                }}
              ></div>
            </div>
          </div>
          <div className='EditorArea'>
            <div className='TitleQuestion'>
              <div className='dropdownDiv'>
                <SelectInput
                  className='LangauageSelect'
                  onChange={(e) => this.setState({ language: e })}
                  options={languageOptions}
                  value={language}
                  placeholder={
                    language && language.label
                      ? language.label
                      : "Select Language"
                  }
                />
              </div>
              <div className='TitleBtnArea' style={{ marginRight: "20px" }}>
                <div className='executionleft'>
                  {countExecute - totalExecuted} Execution left
                </div>
                <Button
                  className='buttonCode'
                  variant='contained'
                  onClick={this.handleRun}
                  disabled={
                    run ||
                    !code ||
                    countExecute - 1 === totalExecuted ||
                    language === null
                  }
                >
                  Run <img src={codeIcon} alt='code icon' />
                </Button>
              </div>
            </div>
            <AceEditor
              className='codeEditor'
              placeholder='Type your code here'
              mode={language && language.lang ? language.lang : ""}
              theme='tomorrow'
              name='codeEditor'
              onLoad={this.onLoad}
              onChange={this.onChange}
              fontSize={14}
              showPrintMargin={true}
              showGutter={true}
              highlightActiveLine={true}
              value={code}
              setOptions={{
                enableBasicAutocompletion: autocomplete,
                enableLiveAutocompletion: autocomplete,
                enableSnippets: true,
                showLineNumbers: true,
                tabSize: 2,
              }}
            />
          </div>
        </div>
        <div className='OtherArea'>
          <div className='TimerVideoArea'>
            <div className='TimerDiv'>
              <div className='TitleQuestion'>
                <div className='textTitle'>Timer</div>
              </div>
              <div className='TimerShow'>
                {duration ? (
                  <TimerComponent
                    durationTime={duration}
                    className='timer-new'
                    isPlaying={isPlaying}
                    disableBtn={restartTimer}
                    nextBtn={true}
                    handleTimer={this.handleTimer}
                    onTick={(time, remainingTime) => {
                      this.handleTimeUpdate(time, remainingTime);
                    }}
                  />
                ) : null}
              </div>
            </div>
            <div className='VideoDiv'>
              <WebcamComponent
                handleStartCaptureClick={this.handleStartCaptureClick}
                handleDataAvailable={this.handleDataAvailable}
                handleStopCaptureClick={this.handleStopCaptureClick}
                capturing={this.state.capturing}
                webcamRef={this.webcamRef}
                recordedChunks={this.state.recordedChunks}
                handleDownload={this.handleDownload}
              />
              <div className='audioDiv'>
                <div className='audio-img'>
                  <img src={audio} alt='' />
                </div>
                {audioNew ? (
                  <div className='audio-analyser'>
                    <AudioAnalyser amplitude={() => null} audio={audioNew} />
                  </div>
                ) : null}
              </div>
            </div>
          </div>
          <div className='IOArea'>
            <div className='IoHeader'>
              <div className='TabSwitch'>
                <div
                  className='Tab'
                  style={{
                    backgroundColor: tab ? "#CADEDB" : "#fff",
                    color: tab ? "#21745D" : "#888888",
                    borderTopLeftRadius: "4px",
                  }}
                  onClick={() => this.setState({ tab: true })}
                >
                  Stdin
                </div>
                <div
                  className='Tab'
                  style={{
                    backgroundColor: !tab ? "#CADEDB" : "#fff",
                    color: !tab ? "#21745D" : "#888888",
                  }}
                  onClick={() => this.setState({ tab: false })}
                >
                  Output Terminal
                </div>
              </div>
              <div style={{ marginRight: "20px" }}>
                <Button
                  className='buttonCode'
                  variant='contained'
                  onClick={this.handleTimer}
                  disabled={
                    (run || language === null || disablebtn || !code) &&
                    countExecute - 1 !== totalExecuted
                  }
                >
                  Submit
                </Button>
              </div>
            </div>
            <div className='IOtextArea'>
              {tab ? (
                <textarea
                  placeholder='Enter Input Here'
                  className='TextareaInput'
                  value={input}
                  onChange={this.inputHandler}
                ></textarea>
              ) : (
                <div className='OutputDiv'>
                  {!run ? (
                    "No ouput"
                  ) : output ? (
                    <pre
                      style={{
                        color:
                          output && output.status_id == 3 ? "#000" : "#E61212",
                      }}
                    >
                      {output && output.stdout && output.status_id == 3
                        ? output.stdout
                        : output && output.stderr && output.status_id == 11
                        ? output.stderr
                        : output &&
                          output.compile_output &&
                          output.status_id == 6
                        ? output.compile_output
                        : ""}
                    </pre>
                  ) : (
                    <div className='loader'>
                      <CircularProgress />
                    </div>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  codeReducer: state.CodeEditorReducer,
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    CodeAction: bindActionCreators(Actions, dispatch),
    UserAction: bindActionCreators(UserActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(CodeEditor));
